.dashboard {
    background-color: rgb(236, 236, 236);
}
.dayboard {
    background-color: white;
    margin-top: 15px;
    -webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
            box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
}
.dayboard p {
    font-size: 24px;
    padding: 15px 15px 20px;
}
.dayboard span {
    font-size: 24px;
    padding: 15px 15px 0px;
    
}
.first-column {
    padding-left: 5px;
}
table {width: 100%;}
thead tr th {
    text-transform: uppercase;
    font-weight: normal;
    text-align: center;
    font-size: 10px;
    border-right: 1px solid grey;
    padding: 5px 0;
}
tbody tr td {
    height: 60px;
    font-size: 12px;
    text-align: center;
}
tbody tr:nth-child(odd) {
    background-color: rgba(127, 221, 233, 0.4);
}
tbody tr td:last-child {
    cursor: pointer;
}
.topic-data {
    margin-top: 15px;
}
.topic-data span {
    font-size: 15px;
}
.topic-data {
    font-size: 15px;
}



