.login-page {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
    min-height: 100vh;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
	background-color: #01bcd5;
}

.login-block {
		color: rgba(0, 0, 0, 0.87); 
		background-color: rgb(255, 255, 255); 
		-webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
		-o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
		transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
		-webkit-box-sizing: border-box; 
		        box-sizing: border-box; 
		font-family: Roboto, sans-serif; 
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0); 
		-webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px; 
		        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px; 
		border-radius: 2px; 
		z-index: 1; 
		min-width: 300px;
}
.login-img-wrap {
	margin: 1em; 
	text-align: center;
}

.login-img {
	width: 50px;
}

.login-text {
	width: 90px;
	margin: 0 auto;
}
.login-wrap {
	display: -ms-flexbox;
	display: flex;
}
.login-input-wrap {
	font-size: 16px; 
	line-height: 24px; 
	width: 100%; 
	height: 72px; 
	display: inline-block; 
	position: relative; 
	background-color: transparent; 
	font-family: Roboto, sans-serif; 
	-webkit-transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	-o-transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	transition: height 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	cursor: auto;
	padding-left: 15px;
}
.login-lable {
	position: absolute; 
	line-height: 22px; 
	top: 38px; 
	-webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	-o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	z-index: 1; 
	-webkit-transform: scale(0.75) translate(0px, -28px); 
	    -ms-transform: scale(0.75) translate(0px, -28px); 
	        transform: scale(0.75) translate(0px, -28px); 
	-webkit-transform-origin: left top 0px; 
	    -ms-transform-origin: left top 0px; 
	        transform-origin: left top 0px; 
	pointer-events: none; 
	-webkit-user-select: none; 
	   -moz-user-select: none; 
	    -ms-user-select: none; 
	        user-select: none; 
	color: rgba(0, 0, 0, 0.3);
}
.login-input {
	padding: 0px; 
	position: relative; 
	width: 100%;
	border: none;
	/*border-bottom: 1px solid rgba(0, 0, 0, 0.3);*/
	outline: none; 
	background-color: rgba(0, 0, 0, 0); 
	color: rgba(0, 0, 0, 0.87); 
	cursor: inherit; 
	font-style: inherit; 
	font-variant: inherit; 
	font-weight: inherit; 
	font-stretch: inherit; 
	font-size: inherit; 
	line-height: inherit; 
	font-family: inherit; 
	opacity: 1; 
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); 
	height: 100%; 
	-webkit-box-sizing: border-box; 
	        box-sizing: border-box; 
	margin-top: 14px;
}
.login-hr {
	margin: -25px 0 0 0;
	padding: 0;
}

.button-wrap {
	padding: 5px;
	margin-bottom: 10px;
	height: 36px; 
	border-radius: 2px; 
	-webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	-o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	top: 0px;
}

.login-button {
	border: 10px; 
	-webkit-box-sizing: border-box; 
	        box-sizing: border-box; 
	display: inline-block; 
	font-family: Roboto, sans-serif; 
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); 
	cursor: pointer; 
	text-decoration: none; 
	margin: 0px; 
	padding: 0px; 
	outline: none; 
	font-size: inherit; 
	font-weight: inherit; 
	position: relative; 
	height: 36px; 
	line-height: 36px; 
	width: 100%; 
	border-radius: 2px; 
	-webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	-o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; 
	background-color: rgb(0, 188, 212); 
	text-align: center;
	color: white;
}

.login-button:hover {
	opacity: 15;
}










